import React, { useEffect, useRef, useState } from 'react';

const MapComponent = ({ onLocationSelect, savedLocation, lat=12.976794,  lng=77.590092, setCurrentLocation}) => {
  const mapRef = useRef(null);
  const [map, setMap] = useState(null);
  const [marker, setMarker] = useState(null);

  const loadGoogleMapsScript = (callback) => {
    const existingScript = document.querySelector('script[src*="maps.googleapis.com"]');
    if (existingScript) {
      if (window.google && window.google.maps) {
        callback();
      } else {
        existingScript.onload = callback;
      }
      return;
    }

    const script = document.createElement('script');
    script.src = `https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}&libraries=places`;
    script.async = true;
    script.defer = true;
    script.onload = callback;
    script.onerror = () => console.error("Failed to load Google Maps script.");
    document.body.appendChild(script);
  };

  useEffect(() => {
    loadGoogleMapsScript(() => {
      if (window.google && window.google.maps) {
        const initialLocation = savedLocation || { lat , lng  };
        const newMap = new window.google.maps.Map(mapRef.current, {
          center: initialLocation,
          zoom: 15,
        });
        setMap(newMap);

        const newMarker = new window.google.maps.Marker({
          position: initialLocation,
          map: newMap,
          draggable: true,
        });
        setMarker(newMarker);

        newMap.addListener("click", (event) => {
          const lat = event.latLng.lat();
          const lng = event.latLng.lng();
          newMarker.setPosition({ lat, lng });
          handleSaveLocation( lat, lng );
        });

        newMarker.addListener("dragend", (event) => {
          const lat = event.latLng.lat();
          const lng = event.latLng.lng();
          newMarker.setPosition({ lat, lng });
          handleSaveLocation(lat, lng); // Save the new marker location
          
        });
      } else {
        console.error("Google Maps library not available.");
      }
    });
  }, [savedLocation, lat, lng]);

  const handleSaveLocation = (lat, lng) => {

      console.log("Lat", lat);
      console.log("lng", lng);
      onLocationSelect({ lat, lng });
 
  };

  return (
    <div>
      <div ref={mapRef} style={{ height: "400px", width: "100%" }} />
      <button
        className="btn btn-primary mt-3"
        onClick={setCurrentLocation}
        style={{ width: "100%" }}
      >
        Use Current Location
      </button>
      {/* <button
        className="btn btn-primary mt-3"
        onClick={handleSaveLocation}
        style={{ width: "100%" }}
      >
        Save Location
      </button> */}
    </div>
  );
};

export default MapComponent;